import uCharts from '@/utils/u-charts'
let deal = null; 
let _self;
export default {
	data () {
		return {
			chartData:{
				cWidth:'',
				cHeight:'',
				pixelRatio:1,
				// 横坐标数据
				categories:['2012', '2013', '2014', '2015', '2016', '2017'],
				//纵坐标数据
				series: [{
					name: '成交量A',
					data: [35, 20, 25, 37, 6, 27],
					color: '#000000'
				}, {
					name: '成交量B',
					data: [70, 40, 65, 100, 44, 68]
				}, {
					name: '成交量C',
					data: [100, 80, 95, 140, 112, 132]
				}]
			},
		}
	}, 
	methods: {
		showLineA(canvasId,chartData){
			deal = new uCharts({
				$this:this,
				canvasId: canvasId,
				dataLabel: false,
				//图的类型
				type: 'line',
				pixelRatio: 1, 
				//横坐标数据
				categories: chartData.categories, 
				// 纵坐标数据
				series: chartData.series,
				//canvas的宽高
				width: _self.chartData.cWidth*_self.chartData.pixelRatio,
				height: _self.chartData.cHeight*_self.chartData.pixelRatio,
			// 横坐标配置
				xAxis: {
					disableGrid: true,
					axisLine: false
				}, 
				yAxis: {
					isAddBottomLine: false,
					gridType: 'dash', 
					gridColor: '#ebebeb', 
					min:0,
					max:180,
					splitNumber: 4,
					format:(val)=>{return val.toFixed(0)}
				}, 
			})
		}
	}, 
	created () {
					_self = this;
					uni.getSystemInfo({
						success: function (res) {
							console.log (res)
							if(res.pixelRatio>1){
								_self.pixelRatio =2;
							}
						}
					});
					this.chartData.cWidth=uni.upx2px(660);
					this.chartData.cHeight=uni.upx2px(428);
					this.showLineA("deal",this.chartData)
	}
}
